import
uniCalendar
from "../../../../components/uni-calendar/uni-calendar"
import Toast from '@/wxcomponents/dist/toast/toast';
export default {
	components: {
		uniCalendar
	},
	data() {
		return {
			dayOfMissionList: [],
			year: '',
			month: '',
			day: '',
			token: '',
			selected: [],
			falg: null,
			myDate: null
		}
	},

	watch: {
		falg(selectDate, beforData) {
			// console.log(selectDate, beforData)
			this.myDate = this.falg
			if (beforData != null) {
				this.getDayOfMissionList()
				if (selectDate.split("-")[0] != beforData.split("-")[0] || selectDate.split("-")[1] != beforData.split("-")[1]) {
					this.taskCalendar()
				}
			}
		},
	},
	created() {
		console.log("Creade")
		uni.getStorage({
			key: 'taskDate',
			success: (data) => {
				// console.log(data)
				this.year = data.data.split("-")[0]
				this.month = data.data.split("-")[1]
				this.day = data.data.split("-")[2]
			}
		});
	},
	onLoad() {
		// console.log("onLode")
		// console.log(this.year,this.month,this.day)
	},
	onShow() {
		this.token = uni.getStorageSync('token')
		if (!this.token) {
			Toast("登录失效，请重新登录！")
		}
		this.taskCalendar()
		this.getDayOfMissionList()
	},
	methods: {
		// 日历
		change(e) {
			// console.log(e)
			this.year = e.year
			this.month = e.month
			this.day = e.date
			// this.taskCalendar()
			this.falg = e.fulldate
			// this.getDayOfMissionList()
			uni.setStorage({
				key: 'taskDate',
				data: e.fulldate
			});
		},
		// 获取任务日历数据
		taskCalendar() {
			this.$minApi.taskCalendar({
				year: this.year,
				month: this.month,
			}).then(data => {
				if (data.code == 200) {
					if (data.data[0]) {
						data.data.forEach(item => {
							var data = {}
							data.date = item.taskTime
							this.selected.push(data)
						})
					}
				}

				// console.log(data)
			})

		},
		// 单日任务详情列表
		getDayOfMissionList() {
			// console.log(this.year, this.month, this.day)
			this.$minApi.dayOfMissionList({
				month: this.month,
				year: this.year,
				day: this.day,
			}, ).then(data => {
				this.dayOfMissionList = data.data
				// console.log(data)
			}).catch(err => {
				// console.log(err)
			})
		},
		// 页面跳转
		linkTo(allWordCount, taskTime, taskId,className) {
			uni.navigateTo({
				url: '../details/operationalDetails?taskId=' + taskId + '&allWordCount=' + allWordCount + '&taskTime=' +
					taskTime+'&className='+className
			})
		}
	},


};
